import React from 'react';
import { Avatar, Stack, Typography, useTheme } from '@mui/material';
import { dateTime } from '../../lib/dateHelper';
import { UIOrderComment } from '@api/v1';
import { AttachmentThumbnail } from './AttachmentThumbnail';

interface MessageProps {
	comment: UIOrderComment;
}

export const Message = ({ comment }: MessageProps) => {
	const theme = useTheme();
	const { avatar, initials, name, type } = comment.author;
	const isRightAligned = type === 'INTERNAL';
	const backGroundColor = isRightAligned
		? theme.palette.secondaryShades8p
		: theme.palette.primaryShades8p;
	return (
		<Stack gap={1} direction={isRightAligned ? 'row-reverse' : 'row'}>
			<Avatar variant="circular" src={avatar}>
				{avatar ? '' : initials}
			</Avatar>
			<Stack
				direction="column"
				gap={0.5}
				alignItems={isRightAligned ? 'flex-end' : 'flex-start'}
				sx={{ flexGrow: 1 }}
			>
				<Typography variant="subtitle2">{name}</Typography>
				<Typography sx={{ borderRadius: 1, background: backGroundColor, p: 1, width: '100%' }}>
					{comment.text}
				</Typography>
				{comment.attachments?.length ? (
					<Stack sx={{ gap: 0.5, width: '100%' }}>
						{comment.attachments.map((a) => (
							<AttachmentThumbnail
								key={a.id}
								link={a.url}
								thumbnail={a.thumbnailS3Key}
								backGroundColor={backGroundColor}
							/>
						))}
					</Stack>
				) : null}
				<Typography variant="caption">{dateTime(comment.createdAt).toFormat('HH:mm')}</Typography>
			</Stack>
		</Stack>
	);
};
